#skills #cards::-webkit-scrollbar-track {
  background-color: transparent;
}
#skills #cards::-webkit-scrollbar {
  width: 8px;
}
#skills #cards::-webkit-scrollbar-thumb {
  background-color: rgba(0,0,0,0.55);
  border-radius: 5px;
}
#skills .side-bar.hide .navbar,
#skills .side-bar .item-collapse ul {
  display: none;
}
#skills .side-bar .item-collapse.expanded > ul,
#skills .skills-card::after {
  display: block;
}
#skills .main {
  padding: 0;
}
#skills .top-bar {
  background-color: #2a2d34;
  width: 100%;
  height: 52px;
}
#skills .top-bar .title {
  background-color: #2a2d34;
  color: #fff;
  font-size: 24px;
  padding: 8px 0;
  text-align: center;
  width: 300px;
}
#skills .side-bar {
  z-index: 1;
  position: absolute;
  background-color: #2a2d34;
  width: 300px;
  cursor: pointer;
  transition: width 0.25s cubic-bezier(0.4, 0, 0.2, 1);
  height: 80vh;
}
#skills .side-bar.hide {
  width: 50px;
}
#skills .side-bar.hide img {
  transform: rotate(-180deg);
}
#skills .side-bar > img {
  width: 40px;
  margin: 0 5px;
  transition-duration: 0.15s;
  float: right;
}
#skills .side-bar .navbar {
  text-align: left;
  cursor: initial;
  margin: 0;
  padding-left: 20px;
  font-size: 16pt;
}
#skills .side-bar .item {
  cursor: pointer;
  color: #fff;
}
#skills .side-bar .item img {
  border-radius: 50%;
  transition-duration: 0.15s;
  float: right;
  width: 30px;
}
#skills .side-bar .item p {
  margin: 0;
}
#skills .side-bar .item-initial {
  color: #fff;
  font-weight: bold;
}
#skills .side-bar .item-initial:first-child {
  margin-top: 2rem;
}
#skills .side-bar .item-collapse.expanded > p > img {
  transform: rotate(-180deg);
}
#skills .side-bar .item-collapse ul {
  font-weight: 500;
}
#skills .side-bar .item-collapse > p > img {
  transform: rotate(0deg);
}
#skills #cards {
  padding-left: 50px;
  width: 100%;
  overflow: auto;
  height: 80vh;
}
#skills #cards .col {
  padding: 10px 20px 20px;
}
#skills #cards .row {
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
}
@media (min-width: 992px) {
  #skills #cards .row {
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -ms-flex-direction: row;
    flex-direction: row;
  }
}
#skills .skills-card {
  padding: 20px;
  box-shadow: 1px 1px 5px 0 rgba(47,57,65,0.5);
  margin: 10px 0;
}
#skills .skills-card::after {
  font-size: 18px;
  position: absolute;
  color: #fff;
  padding: 5px 20px;
  right: 20px;
  transform: translateY(-77px);
  border-radius: 25px 0 0 25px;
}
@media (min-width: 992px) {
  #skills .skills-card::after {
    transform: translateY(-82px);
  }
}
#skills .skills-card.level-1::after {
  content: "Ruim";
  background-color: #ff4343;
}
#skills .skills-card.level-2::after {
  content: "Razoavel";
  background-color: #f90;
}
#skills .skills-card.level-3::after {
  content: "Bom";
  background-color: #79c841;
}
#skills .skills-card.level-4::after {
  content: "Ótimo";
  background-color: #4a86e8;
}
#skills .skills-card .title {
  font-size: 18px;
}
#skills .skills-card .score {
  justify-content: center;
  padding-top: 15px;
}
#skills .skills-card .point {
  margin-right: 2px;
  border-radius: 50%;
  background-color: #ebebeb;
  width: 15px;
  height: 15px;
  float: left;
}
@media (min-width: 992px) {
  #skills .skills-card .point {
    width: 20px;
    height: 20px;
  }
}
#skills .skills-card .point.full {
  background-color: #0d9fff;
}
#skills .skills-card-group {
  padding: 37.5px 20px;
  position: -webkit-sticky;
  position: sticky;
  top: 20px;
}
#skills .skills-card-group::after {
  display: none;
}
#skills .item-collapse ul {
  padding-left: 30px;
}
#skills .side-bar,
#skills #card {
  height: calc(100vh - 52px);
}
@media (min-width: 768px) {
  #skills .side-bar,
  #skills #card {
    height: calc(100vh - 92px);
  }
}
